import React, { useEffect } from 'react'

const KakaoShareButton = () => {
    useEffect(() => {
        createKakaoButton()
    }, [])

    const createKakaoButton = () => {
        if (window.Kakao) {
            const kakao = window.Kakao

            if (!kakao.isInitialized()) {
                kakao.init("897d99703e7ec96b5ac9ad887c3f6ab0")
            }

            kakao.Link.createDefaultButton({
                container: '#kakao-link-btn',
                objectType: 'feed',
                content: {
                    title: '[모바일청첩장] 성래♥지수 결혼식에 초대합니다!',
                    description: '23.02.04 아모리스 역삼 11:30 AM',
                    imageUrl: "https://lh3.googleusercontent.com/pw/AL9nZEXRVCeX8AQjVz6qMpFlwS1h0HrRLBGr_DXVugmC0IVNwu2OsimaDlikMToiRv4YRidBNm_KVB0W8Gz6ozNz5gyDQmjC3Z7onlOH7oxCgkotADaT64ptSJ6mjy_B3ZwLBnOjfBqshMwaqtRMOZ27SzI=w500-h750-no?authuser=1",
                    link: {
                        mobileWebUrl: window.location.href,
                        webUrl: window.location.href,
                    },
                },
                buttons: [
                    {
                        title: '모바일청첩장 보기',
                        link: {
                            mobileWebUrl: window.location.href,
                            webUrl: window.location.href,
                        },
                    }
                ],
            })
        }
    }

    return (
        <div className="KakaoShare" id="kakao-link-btn">
            <text className="KakaoShareText">카카오톡으로 공유하기</text>
            <button className="KakaoShareButton">
                <img className="KakaoShareIcon" src={"/img/kakaoshare_button.png"} alt="kakao-share-icon" />
            </button>
        </div>
    )
}

export default KakaoShareButton